
import {Component, OnInit} from '@angular/core'
import {routerTransition} from './transition.component'
import {FormGroup, FormControl,Validators} from "@angular/forms";
declare let $:any;
declare let google:any;
declare let firebase:any;
import {AuthService} from './access service/access.service'
import {Router} from '@angular/router'
@Component({
  selector:'login',
  templateUrl:'./login.component.html',
  styles:[`
 body {
      background-color: rgba(0,0,0,.87);
    }
    body > .grid {
      height: 100%;
    }
    .image {
      margin-top: -100px;
    }
    .column {
      max-width: 450px;
    }
.masthead.segment {
    min-height: 0px;
  }
  .grid{
  margin-top:9em;
  }
 .tn_focus
 {
  outline: none;
  border-color: red;
  box-shadow: 0 0 10px #9ecaed;
 }
`],

  animations: [routerTransition()]
})
export class  LoginComponent implements OnInit
{

  private myloginform:FormGroup;

  constructor(private acc:AuthService,private router:Router){
    this.myloginform = new FormGroup({
     'email':  new FormControl('',[Validators.required,Validators.pattern("[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?")
     ]),
      'password': new FormControl('',[Validators.required])

    })
  }
  private h :string = 'in';
ngOnInit(){
  $('.ui.modal.selectlocating1').remove();
  $('.ui.modal.selectlocating').remove();
  $('#updatesomething').remove();
  let user = firebase.auth().currentUser;
  if(user)
  {
    this.router.navigate(['./home'])
  }
  if(this.acc.isAuthenticated())
  {
    this.router.navigate(['./home'])
  }

  $('.pusher').css({
    'background-color':'#fed463'
  });

  $('.masthead').css({

    'background-color':'#58585a',


  });

 $('#here').empty();
}

onLoginSubmit(){
$('.logindim.dimmer').dimmer({
  on:false,
  opacity:0.04
}).dimmer('show');
  this.acc.userlogin(this.myloginform.value);


}
wrong(){
  return this.acc.wrong;
}
onFocus()
{

}
}



