import {Component} from '@angular/core'
declare let $:any
declare let anime:any
@Component({
  selector:'reload',
  templateUrl:'./reload.component.html',

})
export class reloadcomponent
{
constructor()
{
  $('.pushermain').remove();
  $(document).ready(function () {

    let i =2;
    setInterval(function () {

      if(i%2 == 0) {
        let alternate = anime({

          targets: ['#alternate'],
          color: [
            {value: '#fed463'}, // Or #FFFFFF
            {value: '#6798d1'},
            {value: '#58585a'},
            {value: '#f26964'}
          ],
          translateX: -500,
          direction: 'alternate',
          duration:3000,


        });
      }
      else {
        let alternate = anime({

          targets: ['#alternate'],
          color: [
            {value: '#fed463'}, // Or #FFFFFF
            {value: '#6798d1'},
            {value: '#58585a'},
            {value: '#f26964'}
          ],
          translateX: 500,
          direction: 'alternate',
          duration:3000,


        });
      }
      i = i+1
    },4000)

  })

}
}



