import {Component, Input, OnInit} from '@angular/core'
declare let google:any;
declare let $:any;
@Component({
  selector:'map',
  templateUrl:'./map.component.html'
})


export class MapComponent implements OnInit{


  @Input() item:any;
public k:number = 0;
public t:number = 0;
public orderdetails:any;
public updateddetails:any =[];
public mapaddress:any ="https://www.google.co.in/maps/dir/";
  ngOnInit(){

    $('.ui.modal.selectlocating1').remove();
    $('.ui.modal.selectlocating').remove();
    $('.locationdim.dimmer').remove();
    $('.ui.modal.test').remove();
    $('.ui.modal.test1').remove();
    $('#updatesomething').remove();
    $('#addess')
      .remove()

    ;
    $('#addees')
      .remove()

    ;
  }

startmap() {

let g = Object.keys(this.item.map).map((k)=> { return this.item.map[k]});


    let directionsDisplay = new google.maps.DirectionsRenderer;
  let directionsService = new google.maps.DirectionsService;


    let map = new google.maps.Map(document.getElementById(this.item.id.toString()), {
      zoom: 3,
      center: {lat: this.item.pos.lat, lng: this.item.pos.lng}
    });
    this.mapaddress = this.mapaddress + this.item.pos.lat + ','+ this.item.pos.lng + '/'+this.item.address+'/'+'@';
    directionsDisplay.setMap(map);
  directionsDisplay.setPanel(document.getElementById(this.item.random));
  this.mapaddress = this.mapaddress + g[0].lat +','+g[0].lng;
if(g.length == 1) {
  directionsService.route({
    origin: {lat: this.item.pos.lat, lng: this.item.pos.lng},
    destination: g[0],

    travelMode: 'DRIVING'
  }, (response: any, status: any) => {
    if (status === 'OK') {
      directionsDisplay.setDirections(response);
    } else {
      window.alert('Directions request failed due to ' + status);
    }
  });

}
else {
g.splice(g.length-1,1)
let f:any = [];
for(let i of g)
{
  let g = {
    'location':i,
    'stopover':true
  }
  f.push(g)
}
  directionsService.route({
    origin: {lat: this.item.pos.lat, lng: this.item.pos.lng},
    destination: g[g.length-1],
    waypoints: f,
    travelMode: 'DRIVING'
  }, (response: any, status: any) => {
    if (status === 'OK') {
      directionsDisplay.setDirections(response);
    } else {
      window.alert('Directions request failed due to ' + status);
    }
  });
}



}
checking() {

  if (this.item.map && this.item.ready) {
    let g = Object.keys(this.item.map).map((k) => {
      return this.item.map[k]
    });
    if (document.getElementById(this.item.id.toString()) && g.length && this.k == 0) {
      this.startmap();
      this.k = 1;

    }
    if (!this.t) {

    let up = Object.keys(this.item.updated).map((k) => {
      return this.item.updated[k]
    })
    for (let i of up) {
      let h = Object.keys(i).map((k) => {
        return i[k]
      })
     for(let j of h)
      this.updateddetails.push(j)

    }
    this.t = 1;

  }

    return true;
  }
  else {
    return false;
  }

}

  see(){

    $('#'+this.item.id+'i').modal({
      observeChanges:true,
    }).modal('show');
  }
}



