import {trigger, state, group,animate, style, transition,keyframes} from '@angular/animations';

export function routerTransition() {
  return slideToLeft();
}

function slideToLeft() {
  return  trigger('squeeState', [


      state('*' , style({ opacity: 1, transform: 'scale(1.0)' })),
      state('void', style({ opacity: 0, transform: 'scale(0.0)'  })),

      transition('void => *', animate('400ms'))
    ])

}



