
import {Component, OnInit} from '@angular/core'
import {routerTransition} from './transition.component'
import {AuthService} from './access service/access.service'
import {FormGroup, FormControl,Validators} from "@angular/forms";
import {dataService} from "./dataservice/data.service";
declare let google:any;

declare let $:any;
@Component({
  selector:'signup',
  templateUrl:'./signup.component.html',
  styles:[`
 body {
      background-color: #DADADA;
    }
    body > .grid {
      height: 100%;
    }
    .image {
      margin-top: -100px;
    }
    .column {
      max-width: 450px;
    }

`],
  animations: [routerTransition()]
})
export class  SignupComponent implements OnInit
{
  public pos:any = {lat:'',lng:''};
  public address:string;
  public results:any;
  private det:any;
  public error:any;
private mysignupform:FormGroup;
constructor(private ae:AuthService,private de:dataService){
 this.mysignupform = new FormGroup({
   'email' :new FormControl('', Validators.compose([
     Validators.required,
     this.isEmail
   ]),
   ),
   'password': new FormControl('',[Validators.required,Validators.minLength(8)]),
   'address':new FormControl({value:'',disabled: true}),
   'name':new FormControl('',[Validators.required,Validators.minLength(8)]),
   'phonenumber':new FormControl('',[Validators.required]),

 })
}
ngOnInit(){
  $('.ui.modal.selectlocating1').remove();
  $('.ui.modal.selectlocating').remove();
  $('#updatesomething').remove();
  this.getbeforeLocations();

  $('.masthead').css({

    'background-color':'#58585a',


  });
  $('.pusher').css({
    'background-color':'#fed463'
  });

  $('#here').empty();
}

  get(s:string){

    this.address = s;
    console.log(this.address);
    console.log($('#signadd'));
    $('#signadd').val(s);

    $('#signadd').value = s;
    $('.ui.modal.something')

      .modal('hide');
  }

  getLocations(){
    if (navigator.geolocation) {
      $('.locationsidim.dimmer').dimmer({
        on:false,
        opacity:0.04
      }).dimmer('show');
      navigator.geolocation.getCurrentPosition((position) =>{
        this.pos= {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        }

        let geocoder = new google.maps.Geocoder;


        geocoder.geocode({'location': this.pos}, (results:any, status:any)=> {
          if (status === 'OK') {

            this.results = results;
            console.log(this.results);


            $('.locationsidim.dimmer').dimmer('hide');
            $('#signsomething').modal({
              observable:true
            }).modal('show')
          }
          else {
            $('.locationsidim.dimmer').dimmer('hide');
            console.log(status);
          }
        })

      }, (error:any) =>{
        $('.locationsidim.dimmer').dimmer('hide');
        console.log(error);
      });
    }
  }
  getbeforeLocations(){
    if (navigator.geolocation) {

      navigator.geolocation.getCurrentPosition((position) =>{


        let geocoder = new google.maps.Geocoder;


        geocoder.geocode({'location': {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        }}, (results:any, status:any)=> {
          if (status === 'OK') {

            this.results = results;
            console.log(this.results);



          }
          else {

            console.log(status);
          }
        })

      }, (error:any) =>{

        console.log(error);
      });
    }
  }
  onsignSubmit(event:any) {
    console.log(this.pos.lat);
    if (this.pos.lat) {
      $('.signupdim.dimmer').dimmer({
        on: false,
        opacity: 0.04
      }).dimmer('show');

      event.preventDefault();

      this.ae.usersignin(this.mysignupform.value,this.det);

      this.det = {
        'name': this.mysignupform.get('name').value,
        'address': $('#signadd').val(),
        'phonenumber': this.mysignupform.get('phonenumber').value,
        'lat': this.pos.lat,
        'lng': this.pos.lng,
        'email': this.mysignupform.get('email').value,
        'orders': [],
        'previous': [
          {
            'name': 'delete this at beginning',
            'quantity': 20
          }
        ]
      }

      console.log(this.det);
    }
    else {
this.error = "Please Click On Get Your Location";
    }
  }

  isEmail(control: FormControl): {[s: string]: boolean} {
    if (!control.value.match(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)) {
      return {noEmail: true};
    }
  }



}



