import {trigger, state, animate, style, transition,keyframes} from '@angular/animations';

export function flyTransition() {
  return slideToLeft();
}

function slideToLeft() {
  return  trigger('herosState', [

    transition('void => *', [
      style({transform: 'translateX(-100%)'}),
      animate(500)
    ])
  ])
}



