import {Component, OnInit} from '@angular/core';
import {dataService} from '../dataservice/data.service'
declare let $:any;
declare let firebase:any;
declare let google:any;
import 'rxjs/Rx'
import {order} from '../dataservice/order'
import {getTransition} from './newtransition.component'
import {data} from "../dataservice/data";
import {ReversePipe} from './reverse.pipe'
@Component({
  selector:'order',
  templateUrl:'./order.component.html',
animations:[getTransition()],

})


export class orderComponent implements OnInit{
  public details:data[];
  public orderdetails:any;
constructor(private data:dataService)
{
  $('.ui.modal.selectlocating1').remove();
  $('.ui.modal.selectlocating').remove();
  $('.locationdim.dimmer').remove();
$('.ui.modal.test').remove();
$('.ui.modal.test1').remove();
  $('#updatesomething').remove();
  $('#addess')
    .remove()

  ;
  $('#addees')
    .remove()

  ;

}


  ngOnInit(){



    $('.masthead').css({

      'background-color':'#58585a',


    });
    $('.pusher').css({
      'background-color':'rgb(103, 152, 209)'
    });

    $('#here').empty();
  this.data.getdata()
    $('.ui.accordion')
      .accordion()
    ;
  }

  getdata(){
this.details = this.data.data;
if(this.details)
{
  this.details = Object.keys(this.data.data).map((k)=> { return this.data.data[k] });
  this.details = Object.keys(this.details[0]).map((k)=> { return this.details[0][k] });

  this.details = Object.keys(this.details[5]).map((k)=> { return this.details[5][k] });

  return true;
}
else
{
  return false;
}
  }

  //------------------------------Map=============================

}



