import {Component, OnInit} from '@angular/core';
import {dataService} from './dataservice/data.service'
declare let $:any;
import {routerTransition} from './squeeze.tranisition'
import {FormGroup, FormControl, Validators} from "@angular/forms";
@Component({
  selector:'cont',
  templateUrl:'./contact.component.html',
  animations:[routerTransition()]
})
export class ContactComponent implements OnInit{
private feedbackform:FormGroup;
private submitted:boolean= false;
constructor(private de:dataService){
  this.feedbackform = new FormGroup({
    'name': new FormControl('',[Validators.required]),
    'email':new FormControl('',[Validators.required]),
    'texts': new FormControl('',[Validators.required])
  })
}

  ngOnInit(){
    $('.ui.modal.selectlocating1').remove();
    $('.ui.modal.selectlocating').remove();
    $('#updatesomething').remove();
    $('.locationdim.dimmer').remove();
    $('.ui.modal.test').remove();
    $('.ui.modal.test1').remove();
    $('#addess')
      .remove()

    ;
    $('#addees')
      .remove()

    ;
    $('.masthead').css({

      'background-color':'#6798d1',


    });
    $('.pusher').css({
      'background-color':'white'
    });

    $('#here').empty();
  }
feedback(){

this.de.feedbacks(this.feedbackform.value);
this.feedbackform.reset();
this.submitted = true;
}
}



