import { NgModule }      from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';

import { AppComponent }  from './app.component';
import {FormsModule,ReactiveFormsModule} from "@angular/forms";
import {HttpModule} from '@angular/http'
import {LoginComponent} from './login.component'
import {RouterModule} from '@angular/router'
import {MainComponent} from './main.component'
import {SignupComponent} from './signup.component'
import {HelloComponent} from './hello.component'
import {HomeComponent} from './afterLogin/home.component'
import {orderComponent} from "./afterLogin/order.component";
import {dataService} from './dataservice/data.service'
import {AuthService} from './access service/access.service';
import {ContactComponent} from './contactus.component';
import {AuthGuard} from './access service/auth.guard'
import {closeGuard} from './access service/close.guard'
import {MapComponent} from './afterLogin/map.component'
import {ReversePipe} from './afterLogin/reverse.pipe';
import { ForgotpasswordComponent } from './access service/forgotpassword/forgotpassword.component'
import {profileComponent} from "./afterLogin/profile.component";
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";
import {reloadcomponent} from './reloadcomponent'
import {OnFocusDirective} from './onfocus.directive'
@NgModule({
  imports:      [BrowserModule,HttpModule,ReactiveFormsModule,FormsModule,BrowserAnimationsModule,RouterModule.forRoot([
    {path:'',redirectTo:'/index',pathMatch:'full'},
    {path:'index',component:MainComponent,canActivate:[closeGuard]},
    {path:'home',component:HomeComponent,canActivate:[AuthGuard]},
    {path:'login',component:LoginComponent,canActivate:[closeGuard]},
    {path:'signup',component:SignupComponent,canActivate:[closeGuard]},
    {path:'orders',component:orderComponent,canActivate:[AuthGuard]},
    {path:'contactus',component:ContactComponent},
    {path:'forgotpassword',component:ForgotpasswordComponent,canActivate:[closeGuard]},
    {path:'profile',component:profileComponent,canActivate:[AuthGuard]},
    {path:'reload',component:reloadcomponent}
  ])],

  declarations: [OnFocusDirective,ReversePipe,ForgotpasswordComponent,profileComponent, reloadcomponent,AppComponent,MapComponent,orderComponent,ContactComponent,HomeComponent,HelloComponent,SignupComponent,MainComponent,LoginComponent, ForgotpasswordComponent],


  bootstrap:    [ AppComponent ],
providers:[dataService,AuthService,AuthGuard,closeGuard],


})
export class AppModule { }



