import {Component, OnInit} from '@angular/core';
import {routerTransition} from './transition.component'
import {dataService} from "./dataservice/data.service";
import {Router} from "@angular/router";
import {AppComponent} from './app.component'

declare let  $:any;


@Component({
  selector: 'main',
  templateUrl:"./main.component.html",
styleUrls:['./main.css'],
  animations:[routerTransition()]

})
export class MainComponent implements OnInit{

private h:string  ='in';
private l:AppComponent;
  constructor(private router:Router,private de:dataService)
  {

  }
  ngOnInit(){
    $('.masthead').css({

      'background-color':'#6798d1',


    });

let f = this.de.fsize - $('.pushermain').height();
console.log(this.de.fsize);
console.log(f);
if($(window).width() < 990)
{
  $('#homepage').css({
    'height':'auto'
  })
  console.log("hi")
  $('.pathimg').hide();
  $('.straight').show();
}
else {

  $('#homepage').css({
    'height':f
  })
  console.log("no")
  $('.pathimg').show()
  $('.straight').hide();
}
    $(window).resize(()=> {
  console.log($(window).width())
      if($(window).width() < 980)
      {
        $('#homepage').css({
          'height':'auto'
        })
        console.log("hi")
        $('.pathimg').hide();
        $('.straight').show();
      }
      else {

        $('#homepage').css({
          'height':f
        })
        console.log("no")
        $('.pathimg').show()
        $('.straight').hide();
      }
})


    $('.ui.modal.selectlocating').remove();
    $('.ui.modal.selectlocating1').remove();
    $('#updatesomething').remove();
$(document).ready(function () {
  $('#signsomething').remove();


  $('.mainhead').css({
    'font-family': '"Gloria Hallelujah", cursive'
  })





  $('.below1').typed({
    strings: ["Progress isn't made by early risers. It's made by lazy men trying to find easier ways to do something."],
    typeSpeed: 1
  });
  $('.typed-cursor').remove()
})

  }






}



