import {Component} from '@angular/core'

import {routerTransition} from "../transition.component";
import {FormGroup, FormControl, Validators} from "@angular/forms";
import {dataService} from "../dataservice/data.service";
declare let $:any;
declare let google:any;
declare let firebase:any;
@Component({
  selector:'profile',
  templateUrl:'./profile.component.html',
  animations:[routerTransition()]

})
export class profileComponent
{
  private deform:FormGroup;
  private passwordform:FormGroup;
  private k:number = 0;
  private checkdet:boolean = false;
  private checkpass:boolean = false;
  private checkloc:boolean = false;
  private pos:any;
  private results:any;
  private address:string;
  private success:boolean = false;
  private error:boolean =false;
  private same:boolean = false;
  private detfo:any ;
  private sameloc:boolean = false;
  private locfo:any;
  constructor(private data:dataService){

    $('.ui.modal.selectlocating').remove();
    $('.locationdim.dimmer').remove();
    $('.ui.modal.test').remove();
    $('.ui.modal.test1').remove();
    $('#updatesomething').remove();
    $('#addess')
      .remove()

    ;
    $('#addees')
      .remove()

    ;
    this.deform = new FormGroup({
      'name': new FormControl('',[Validators.required]),
      'phonenum': new FormControl('',[Validators.required])
    })
    this.passwordform =  new FormGroup({
      'newpassword': new FormControl(''),
      'checkpassword': new FormControl('')
    })
  }
  checking(){
    if(this.k == 0 && this.data.data)
    {
      let g: any = Object.keys(this.data.data).map((k) => {
        return this.data.data[k];
      })
      g = g[0];
      this.deform.setValue({
        name: g.name,

        phonenum: g.phonenumber,

      });
      this.detfo = this.deform.value;
      $('#updateloc').val(g.address);

      $('#updateloc').value = g.address;
      this.locfo = $('#updateloc').val();
      this.k = 1;
    }
  }
  make()
  {
   this.checkdet = true;
  }
  makepass()
  {
    this.checkpass = true;
  }
  makeloc()
  {
    this.checkloc = true;
  }
  back(){
    this.checkdet = false;
  }
  backpass(){
    this.checkpass = false;
  }
  backloc(){
    this.checkloc = false;
  }
updatedet(){


    if(this.detfo.name == this.deform.get('name').value && this.detfo.phonenum == this.deform.get('phonenum').value)
    {
      this.checkdet = false;
      this.same = true;
      setTimeout(()=>
      {
        this.same =false;
      },5000)
    }
    else {
      $('.updatingdetails.dimmer').dimmer('show');
      this.checkdet = false;

      this.data.updatedetailss(this.deform.get('name').value, this.deform.get('phonenum').value)
    }

}
updatepass(){

  $('.updatingdetails.dimmer').dimmer('show');
  this.checkpass= false;
  let user = firebase.auth().currentUser;


  user.updatePassword(this.passwordform.get('newpassword').value).then((data)=> {
console.log(data);
    $('.updatingdetails.dimmer').dimmer('hide');
this.data.success = true;
setTimeout(()=>{
  this.data.success = false;
},5000)
  }, (error)=> {
    console.log(error)
    $('.updatingdetails.dimmer').dimmer('hide');
   this.data.error = true;
   setTimeout(()=>
    {
      this.data.error = false;
    },5000)
  });
  this.passwordform.reset();
}
updateloc(){
  if(this.locfo == $('#updateloc').val())
  {
    this.checkloc = false;
    this.sameloc = true;
    setTimeout(()=>{
      this.sameloc = false;
    },5000)
  }
  else {
    if (this.pos.lat) {
      $('.updatingdetails.dimmer').dimmer('show');
      this.checkloc = false;
      this.data.updatelocation(this.pos, this.address);
    }
    else {
      this.error = true;
      setTimeout(() => {
        this.error = false;
      }, 5000)
    }
  }
}
matchs(){
if(this.passwordform.get('newpassword').value==this.passwordform.get('checkpassword').value)
{

  $('.refie').removeClass('error');
  return true;
}
else {
  $('.refie').addClass('error');

  return false;
}
}
  get(s:string){

    this.address = s;
    console.log(this.address);
    console.log($('#signadd'));
    $('#updateloc').val(s);

    $('#updateloc').value = s;
    $('#updatesomething').modal('hide');
  }
  getLocations(){
    if (navigator.geolocation) {
      $('.locupdate.dimmer').dimmer({
        on:false,
        opacity:0.04
      }).dimmer('show');
      navigator.geolocation.getCurrentPosition((position) =>{
        this.pos= {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        }

        let geocoder = new google.maps.Geocoder;


        geocoder.geocode({'location': this.pos}, (results:any, status:any)=> {
          if (status === 'OK') {

            this.results = results;
            console.log(this.results);


            $('.locupdate.dimmer').dimmer('hide');
            $('#updatesomething').modal({
              observable:true
            }).modal('show');
            $('#updatesomething').modal({
              observable:true
            }).modal('refresh')
          }
          else {
            $('.locupdate.dimmer').dimmer('hide');
            console.log(status);
          }
        })

      }, (error:any) =>{
        $('.locupdate.dimmer').dimmer('hide');
        console.log(error);
      });
    }
  }
}



